import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router);

export default new Router({
    routes: [
        {path: '/', redirect: "/login"},
        {path: "/login", component: () => import('./views/login.vue')},
        {
            path: '/home', component: () => import('./views/Home.vue'), children: [
                {
                    path: "article-list/:id",
                    component: () => import('./views/article.list.vue'),
                    props: true
                }
            ]
        },
        {
            path: '/back-manager',
            component: () => import('./views/back.manager.vue'),
            children: [
                {path:"",redirect:"project-list"},
                {path: "project-list", component: () => import('./views/project.list.vue')},
                {path: "person-info", component: () => import('./views/person.info.vue')},
                {path: "publish-article-list", component: () => import('./views/article.list.vue')},
            ]
        }
    ]
})
