import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './registerServiceWorker'
import './plugins/element.js'
import authority from './utils/authority.util'

import "./assets/style/global.scss"
import "./assets/style/bootstrap/css/bootstrap.css"
import Bmob from 'hydrogen-js-sdk'

Vue.config.productionTip = false;

router.beforeEach((to, from, next) => {
    // console.log(to);
    // console.log(from);
    // console.log(authority.isLogin());
    // console.log(Bmob.User.current());
    if (to.path !== "/login" && Bmob.User.current()===null) {
        next({path: "/login", replace: true});
    }
    else if(to.path === "/login" && Bmob.User.current()!==null){
        next({path: "/home", replace: true});
    }
    else{
        next();
    }
});

Bmob.initialize("a154cd1c5e579f7427cebd6dc2c82cc3", "6a615ca8fc70f881da9d351dbc6b9f29");
// 挂载到全局使用
Vue.prototype.Bmob = Bmob;

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app');
